"use client";

import { useEffect, useState } from "react";

type Stats = {
  totalPatients: number;
  averageAge: number;
  diagnoses: { diagnosis: string; _count: { diagnosis: number } }[];
};

export default function DashboardPage() {
  const [stats, setStats] = useState<Stats | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch("/api/stats")
      .then((res) => res.json())
      .then((data) => {
        setStats(data);
        setLoading(false);
      })
      .catch((error) => {
        console.error("Erreur de récupération des statistiques :", error);
        setLoading(false);
      });
  }, []);

  if (loading) return <p>Chargement des statistiques...</p>;

  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
  <div className="p-6 bg-white rounded-2xl shadow flex flex-col items-center justify-center">
    <h2 className="text-sm font-semibold text-gray-500">Total Patients</h2>
    <p className="text-4xl font-bold text-blue-600">{stats?.totalPatients}</p>
  </div>

  <div className="p-6 bg-white rounded-2xl shadow flex flex-col items-center justify-center">
    <h2 className="text-sm font-semibold text-gray-500">Âge Moyen</h2>
    <p className="text-4xl font-bold text-green-600">
      {Number.isFinite(stats?.averageAge) ? stats.averageAge.toFixed(1) : "N/A"}
    </p>
  </div>

  <div className="p-6 bg-white rounded-2xl shadow">
    <h2 className="text-sm font-semibold text-gray-500 mb-2">Diagnostics</h2>
    <ul className="space-y-1">
  {Array.isArray(stats?.diagnoses) && stats.diagnoses.length > 0 ? (
    stats.diagnoses.map((d) => (
      <li key={d.diagnosis} className="text-gray-700">
        {d.diagnosis} : <strong>{d._count.diagnosis}</strong>
      </li>
    ))
  ) : (
    <li className="text-gray-400 italic">Aucun diagnostic trouvé.</li>
  )}
</ul>

  </div>
</div>

  );
}
